<template>
  <div class="ye3">
    <div class="box">
      <div id="main"></div>
      <div id="main2"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  data() {
    return {
      sj: [],
    };
  },
  methods: {},
  mounted() {
    // 圆柱
    var chartDom = document.getElementById("main");
    var myChart = echarts.init(chartDom);
    var option;

    option = {
      title: {
        text: "技能圆柱图",
        // subtext: "Fake Data",
        left: "center",
      },
      grid: {
        left: "80",
      },
      xAxis: {
        type: "category",
        data: ["HTML", "CSS", "JS", "JQ", "uniApp", "es6", "echarts", "Ant-X6"],
      },
      yAxis: {
        type: "value",
        axisLabel: {
          //y轴设置为%
          show: true,
          interval: "auto",
          formatter: function (value, index) {
            if (index == 0) {
              value = 0;
            } else if (index == 1) {
              value = 20;
            } else if (index == 2) {
              value = 40;
            } else if (index == 3) {
              value = 60;
            } else if (index == 4) {
              value = 80;
            } else if (index == 5) {
              value = 100;
            }
            return value + "%";
          },
          // formatter: function (value) {
          //   if (value * 100 > 80) {
          //     return value+ "精通";
          //   } else if (60 < value * 100 < 80) {
          //     return value + "熟练";
          //   } else if (40 < value * 100 < 60) {
          //     return value + "熟练";
          //   } else if (20 < value * 100 < 40) {
          //     return value + "熟悉";
          //   }
          // },
        },
        max: 100, //最大值
        min: 0, //最小值
      },
      series: [
        {
          data: [100, 100, 80, 80, 80, 80, 80, 80],
          type: "bar",
          showBackground: true,
          backgroundStyle: {
            color: "rgba(180, 180, 180, 0.2)",
          },
        },
      ],
    };
    option && myChart.setOption(option);

    // 折线
    var chartDom2 = document.getElementById("main2");
    var myChart2 = echarts.init(chartDom2);
    var option2;

    option2 = {
      title: {
        text: "技能折线图",
        left: "center",
      },
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "shadow",
        },
        formatter: function (params) {
          console.log(params);
          return params[0].name + "<br/>" + params[0].data + "% ";
        },
      },
      xAxis: {
        type: "category",
        data: ["HTML", "CSS", "JS", "JQ", "uniApp", "es6", "echarts", "Ant-X6"],
      },
      yAxis: {
        type: "value",
        show: true,
        interval: "auto",
        formatter: function (value, index) {
          if (index == 0) {
            value = 0;
          } else if (index == 1) {
            value = 20;
          } else if (index == 2) {
            value = 40;
          } else if (index == 3) {
            value = 60;
          } else if (index == 4) {
            value = 80;
          } else if (index == 5) {
            value = 100;
          }
          return value + "%";
        },
        max: 100, //最大值
        min: 0, //最小值
      },
      series: [
        {
          data: [100, 100, 80, 80, 80, 80, 80, 80],
          type: "line",
          smooth: true,
        },
      ],
    };
    option2 && myChart2.setOption(option2);

    let arr = [];
    let arr2 = [];
    // setInterval(() => {
    var s = Math.round(Math.random() * 200);
    arr.push(s);
    arr2.push(Math.round(Math.random() * 1500));
    if (arr.length >= option.series[0].data.length) {
      option.series[0].data = arr;
      option2.series[0].data = arr2;
      // console.log(option.series[0].data, arr);
      arr = [];
      arr2 = [];
      option && myChart.setOption(option);
      option2 && myChart2.setOption(option2);
    }
    // }, 2000);
  },
};
</script>

<style scoped lang="less">
.box {
  padding-left: 10px;

  // display: flex;
  // padding: 50px 200px 0 200px;
}
#main,
#main2 {
  width: 800px;
  height: 500px;
}
</style>
